import React from "react";

import logo1_small from "../img/logo-green-small-1x.png";
import logo2_small from "../img/logo-green-small-2x.png";
import logo1 from "../img/logo-green-1x.png";
import logo2 from "../img/logo-green-2x.png";

const Footer = () => {

    return (
        <footer className="footer">
            <div className="footer__logo-box">
                <picture className="footer__logo">
                    <source srcSet={`${logo1_small} 1x, ${logo2_small} 2x`}
                            media="(max-width: 37.5em)"/>
                    <img srcSet={`${logo1} 1x, ${logo2} 2x`} alt="Full logo"
                         src={logo2}/>
                </picture>

            </div>
            <div className="row">
                <div className="col-1-of-2">
                    <div className="footer__navigation">
                        <ul className="footer__list">
                            <li className="footer__item"><a href="/#" className="footer__link">Company</a></li>
                            <li className="footer__item"><a href="/#" className="footer__link">Contact us</a></li>
                            <li className="footer__item"><a href="/#" className="footer__link">Careers</a></li>
                            <li className="footer__item"><a href="/#" className="footer__link">Privacy policy</a></li>
                            <li className="footer__item"><a href="/#" className="footer__link">Terms</a></li>
                        </ul>
                    </div>
                </div>
                <div className="col-1-of-2">
                    <p className="footer__copyright">
                        Built by <a href="/#" className="footer__link">Tide Solutions.</a>
                    </p>
                </div>
            </div>
        </footer>
    )

}

export default Footer;