import React from "react";


const Story = (props) => {
    return (
        <div className="row">
            <div className="story">
                <figure className="story__shape">
                    <img className="story__img" src={props.details.image} alt={props.details.name}/>
                    <figcaption className="story__caption">
                        {props.details.name}
                    </figcaption>
                </figure>
                <div className="story__text">
                    <h3 className="heading-tertiary u-margin-bottom-small">
                        {props.details.text}
                    </h3>
                    <p>
                        Lorem ipsum dolor sit amet, consectetur adipisicing elit. Consectetur laboriosam nesciunt
                        veritatis?
                        Corporis cumque dolor doloribus fuga illo ipsam laudantium, nam nulla voluptatibus! Animi
                        assumenda
                        cum dolores exercitationem modi nisi odit possimus reiciendis sint, ut!
                    </p>
                </div>
            </div>
        </div>
    )
}

export default Story;