import React from "react";
import Card from "../Card";

const cards = [{
    heading: "The sea explorer",
    number: "1",
    duration: "3",
    people: "30",
    guides: "2",
    accommodation: "cozy hotels",
    difficulty: "easy",
    price: 297
}, {
    heading: "The forest hiker",
    number: "2",
    duration: "7",
    people: "40",
    guides: "6",
    accommodation: "provided tents",
    difficulty: "medium",
    price: 497
}, {
    heading: "The snow adventurer",
    number: "3",
    duration: "5",
    people: "15",
    guides: "3",
    accommodation: "provided tents",
    difficulty: "hard",
    price: 897
}]

const Tours = () => {

    return (
        <section className="section-tours" id="tours">
            <div className="u-center-text u-margin-bottom-big">
                <h2 className="heading-secondary">
                    Most popular tours
                </h2>
            </div>
            <div className="row">
                <div className="col-1-of-3">
                    <Card details={cards[0]}/>
                </div>

                <div className="col-1-of-3">
                    <Card details={cards[1]}/>
                </div>

                <div className="col-1-of-3">
                    <Card details={cards[2]}/>
                </div>
            </div>
            <div className="u-center-text u-margin-top-x-large">
                <a href="/#" className="btn btn--green">Discover all tours</a>
            </div>
        </section>
    )
}

export default Tours;