import React from "react";
import Story from "../Story";
import nat8 from "../../img/nat-8.jpg";
import nat9 from "../../img/nat-9.jpg";
import mp4 from "../../img/video.mp4";
import webm from "../../img/video.webm";

const people = [{
    image: nat8,
    name: "Michelle Watson",
    text: "I had the best week ever with my family"
}, {
    image: nat9,
    name: "John Smith",
    text: "I had the best week ever with my family"
}]

const Stories = () => {
    return (
        <section className="section-stories" id="stories">
            <div className="bg-video">
                <video className="bg-video__content" autoPlay loop muted>
                    <source src={mp4} type="video/mp4"/>
                    <source src={webm} type="video/webm"/>
                    Your browser is not supported!
                </video>
            </div>
            <div className="u-center-text u-margin-bottom-big">
                <h2 className="heading-secondary">
                    We make people genuinely happy
                </h2>
            </div>
            <Story details={people[0]}/>
            <Story details={people[1]}/>
            <div className="u-center-text u-margin-bottom-x-large">
                <a href="/#" className="btn-text">
                    Read All Stories &rarr;
                </a>
            </div>
        </section>
    )
}

export default Stories;