import React from "react";

const Card = (props) => {

    return (
        <div className="card">
            <div className="card__side card__side--front">
                <div className={`card__picture card__picture--${props.details.number}`}>
                    &nbsp;
                </div>
                <h4 className="card__heading">
                    <span className={`card__heading-span card__heading-span--${props.details.number}`}>
                        {props.details.heading}
                    </span>
                </h4>
                <div className="card__details">
                    <ul>
                        <li>{props.details.duration} day tour</li>
                        <li>Up to {props.details.people} people</li>
                        <li>{props.details.guides} tour guides</li>
                        <li>Sleep in {props.details.accommodation}</li>
                        <li>Difficulty: {props.details.difficulty}</li>
                    </ul>
                </div>
            </div>
            <div className={`card__side card__side--back card__side--back-${props.details.number}`}>
                <div className="card__cta">
                    <div className="card__price-box">
                        <p className="card__price-only"> Only </p>
                        <p className="card__price-value">
                            ${props.details.price}
                        </p>
                    </div>
                    <a href="natours/#popup" className="btn btn--white">Book now!</a>
                </div>
            </div>
        </div>
    )
}

export default Card;